.body {
  background-color: #ECECEC;
}

.header {
  height: 100vh;
}
.header::before {
  content: "";
  background-image: url(../../../../build/img/img1.jpg);
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.7;
}
.header__box {
  position: relative;
  margin-top: 20rem;
}
@media (min-width: 1600px) {
  .header__box {
    margin-top: 30rem;
  }
}
.header__title {
  max-width: 40rem;
  font-size: 4.2rem;
}
.header__title span {
  color: #0069FF;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 6.2rem;
    max-width: 60rem;
  }
}
@media (min-width: 1600px) {
  .header__title {
    font-size: 8rem;
    max-width: 80rem;
  }
}
.header__parragraph {
  line-height: 1.5;
}
@media (min-width: 768px) {
  .header__parragraph {
    max-width: 40rem;
  }
}
@media (min-width: 992px) {
  .header__parragraph {
    max-width: 60rem;
  }
}
.header__buttons {
  display: flex;
  gap: 2rem;
}
.header__cta {
  text-decoration: none;
  padding: 1.5rem 5rem;
  border-radius: 0.5rem;
  color: #fff;
  background-color: #0069FF;
  transition: background-color;
  transition-duration: 0.3s;
}
.header__cta--buy:hover {
  background-color: #0955c0;
}
.header__cta--sell {
  background-color: #ECECEC;
  color: #000;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.header__cta--sell:hover {
  background-color: rgb(220, 220, 220);
}

.nav {
  height: 7rem;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
}
@media (min-width: 992px) {
  .nav {
    overflow: visible;
    padding: 5rem 0 0;
  }
}
.nav__hamburguer {
  width: 3rem;
  height: 50%;
  background-image: url(../../../../build/img/menu-right.png);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  z-index: 100;
  transition: background-image 0.3s;
}
@media (min-width: 992px) {
  .nav__hamburguer {
    display: none;
  }
}
.nav__hamburguer--open {
  background-image: url(../../../../build/img/bx-x.svg);
}
.nav__overlay {
  background-color: rgba(0, 0, 0, 0.6549019608);
  position: fixed;
  inset: 0;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s opacity;
}
@media (min-width: 992px) {
  .nav__overlay {
    position: unset;
    background-color: transparent;
    opacity: 1;
    pointer-events: unset;
  }
}
.nav__overlay--show {
  opacity: 1;
  pointer-events: unset;
}
.nav__menu {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 65%;
  background-color: #fff;
  max-width: 40rem;
  padding: 7rem 0;
  list-style: none;
  overflow: auto;
}
@media (min-width: 992px) {
  .nav__menu {
    position: unset;
    width: 100%;
    max-width: unset;
    background-color: unset;
    padding: 0;
    overflow: unset;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.nav__item {
  width: 80%;
  margin: 0 auto;
  color: #000;
  cursor: pointer;
  padding: 2rem 0;
}
@media (min-width: 992px) {
  .nav__item {
    width: max-content;
    margin: 0;
    padding: 0;
  }
}
.nav__link {
  text-align: center;
  display: block;
  margin-bottom: 3rem;
  text-decoration: none;
  color: #000;
}
.nav__link--btn {
  background-color: #000;
  color: #fff;
  padding: 2rem 2rem;
  transition: 0.3s opacity;
}
.nav__link--btn:hover {
  opacity: 0.8;
}
@media (min-width: 992px) {
  .nav__link {
    display: inline-block;
    margin-bottom: 0;
  }
  .nav__link--bold {
    font-weight: 700;
  }
}

.section1__title {
  text-align: center;
  font-size: 3.2rem;
  margin-top: 4rem;
}
@media (min-width: 992px) {
  .section1__title {
    font-size: 4.6rem;
  }
}
.section1__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
@media (min-width: 992px) {
  .section1__grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card {
  background-color: #fff;
  text-align: center;
  margin-bottom: 3rem;
  padding: 3rem 2rem;
  min-height: 35rem;
}
.card__img {
  margin: 0 auto;
  margin-top: 2rem;
  width: 8rem;
}
.card__title {
  font-size: 2.5rem;
  margin-top: 0.8rem;
}
.card__parragraph {
  margin-bottom: 3rem;
}
.card__cta {
  text-decoration: none;
  color: #0069FF;
}